import React, { useState } from "react";
import ProductTable from "./ProductTable";
import ProductCreate from "./ProductCreate";
import FlashMessage from "react-flash-message";
import ProductEdit from "./ProductEdit";
// import { Modal, Button } from "react-modal-bootstrap";
import Message from "./common/Message";

const ProductList = () => {
  const ProductData = [
    {
      id: 1,
      name: "Iphone 11",
      price: 12000000,
      detail: "iphone 11 pro max",
      is_show: true
    },
    {
      id: 2,
      name: "Iphone 4",
      price: 2000000,
      detail: "iphone 4 cui",
      is_show: true
    },
    {
      id: 3,
      name: "Iphone 5",
      price: 3000000,
      detail: "iphone 5s",
      is_show: true
    },
    {
      id: 4,
      name: "Iphone 6",
      price: 4000000,
      detail: "iphone 6 plus",
      is_show: true
    },
    {
      id: 5,
      name: "Samsung note 9",
      price: 11000000,
      detail: "Samsung note 9",
      is_show: true
    },
    {
      id: 6,
      name: "Iphone 12",
      price: 32000000,
      detail: "iphone 12 mini",
      is_show: true
    },
    {
      id: 7,
      name: "vsmart live 4",
      price: 5000000,
      detail: "Hang Viet Nam",
      is_show: true
    }
  ];
  const [products, setProducts] = useState(ProductData);
  const [message, setMessage] = useState("");
  const [currentProduct, setCurrentProduct] = useState();
  const [editing, setEditing] = useState(false);
  const createProduct = (product) => {
    product.id = products.length + 1;
    product.is_show = true;
    setProducts([...products, product]);
    setMessage("Create product successfully!");
    console.log(message);
  };

  const editProduct = (product) => {
    setEditing(true);
    setCurrentProduct({
      id: product.id,
      name: product.name,
      price: product.price,
      detail: product.detail
    });
  };

  const FilterName = (keySearch) => {
    const filterProducts = products.map((product) => {
      product.is_show =
        product.name.toLowerCase().search(keySearch.toLowerCase()) !== -1;
      return product;
    });

    setProducts(filterProducts);
  };

  const updateProduct = (id, updateProduct) => {
    const updateProducts = [...products];
    const productIndex = updateProducts.findIndex(
      (product) => product.id === id
    );
    if (productIndex !== -1) {
      updateProducts[productIndex] = updateProduct;
      setProducts(updateProducts);
    }
    setMessage("Update product successfully!");
    console.log(message);
  };

  const deleteProduct = (id) => {
    setEditing(false);
    setProducts(products.filter((product) => product.id !== id));
  };

  return (
    <React.Fragment>
      {editing ? (
        <ProductEdit
          editing={editing}
          setEditing={setEditing}
          currentProduct={currentProduct}
          updateProduct={updateProduct}
        />
      ) : (
        <ProductCreate addProduct={createProduct} />
      )}
      <hr />
      <ProductTable
        products={products}
        editProduct={editProduct}
        deleteProduct={deleteProduct}
        FilterName={FilterName}
      />
      {message && (
        <FlashMessage duration={5000} persistOnHover={true}>
          <Message content={message} />
        </FlashMessage>
      )}
    </React.Fragment>
  );
};

export default ProductList;
